import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { StockComponent } from './stock.component';
import { RouterModule, Routes } from '@angular/router';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';
const routes: Routes = [
  {
    path: "",
    component: StockComponent
  },
  {
    path: "inventory",
    loadChildren: () => import("./inventory/inventory.module").then(m => m.InventoryModule)
  },
  {
    path: "warehouse",
    loadChildren: () => import("./warehouse/warehouse.module").then(m => m.WarehouseModule)
  },
  {
    path: "report-inventory",
    loadChildren: () => import("./report-inventory/report-inventory.module").then(m => m.ReportInventoryModule)
  },
  {
    path: "delivery",
    loadChildren: () => import("./carrying-trade/carrying-trade.module").then(m => m.CarryingTradeModule)
  },
  {
    path: "receive",
    loadChildren: () => import("./receive-the-goods/receive-the-goods.module").then(m => m.ReceiveTheGoodsModule)
  },
  {
    path: "goods-control",
    loadChildren: () => import("./goods-control/goods-control.module").then(m => m.GoodsControlModule)
  },
  {
    path: "balance-stock",
    loadChildren: () => import("./balance-stock/balance-stock.module").then(m => m.BalanceStockModule)
  },
  {
    path: "manage-lots",
    loadChildren: () => import("./manage-lots/manage-lots.module").then(m => m.ManageLotsModule)
  },
  {
    path: "destroy-product",
    loadChildren: () => import("./destroy-lot-product/destroy-lot-product.module").then(m => m.DestroyLotProductModule)
  },
]

@NgModule({
  declarations: [StockComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    TranslateModule
  ]
})
export class StockModule { }
