import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAuth, VhQuerySales } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-stock',
  templateUrl: './stock.component.html',
  styleUrls: ['./stock.component.scss']
})
export class StockComponent implements OnInit {
  management_product_lot: boolean = this.vhQuerySales.getLocalAppSettingNameBranch('permission_branch').management_product_lot
  constructor(
    private router: Router,
    private vhAuth: VhAuth,
    public languageService: LanguageService,
    public vhComponent: VhComponent,
    private vhQuerySales:VhQuerySales
  ) { }

  ngOnInit(): void {
  }
  gotoInventory() {
    if (this.vhAuth.checkMyPermission("management_enable_inventory")) this.router.navigate(["/sales/dashboard/manage/stock/inventory"]);
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  gotoStock() {
    if (this.vhAuth.checkMyPermission("management_enable_stock")) this.router.navigate(["/sales/dashboard/manage/stock/warehouse"]);
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  gotoReportInventory() {
    if (this.vhAuth.checkMyPermission("management_enable_inventory_report")) this.router.navigate(["/sales/dashboard/manage/stock/report-inventory"]);
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  gotoCarryingTrade() {
    if (this.vhAuth.checkMyPermission("branch_management_delivery")) this.router.navigate(["/sales/dashboard/manage/stock/delivery"]);
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  gotoReceiveTheGoods() {
    if (this.vhAuth.checkMyPermission("branch_management_receive_goods")) this.router.navigate(["/sales/dashboard/manage/stock/receive"]);
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  
  /**Chuyển tới trang đối soát hàng hóa */
  goToInvoiceControl() {
    if (this.vhAuth.checkMyPermission("management_goods_control")) this.router.navigate(['/sales/dashboard/manage/stock/goods-control']);
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }

  gotoBalanceInventory(){
    if (this.vhAuth.checkMyPermission("management_balance_stock")) this.router.navigate(['/sales/dashboard/manage/stock/balance-stock']);
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }

  gotoManageLots(){
    if (this.vhAuth.checkMyPermission("management_lots")) this.router.navigate(['/sales/dashboard/manage/stock/manage-lots']);
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }

  gotoDestroyProduct(){
    if (this.vhAuth.checkMyPermission("management_destroy_product")) this.router.navigate(['/sales/dashboard/manage/stock/destroy-product']);
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
}
